<template>
    <div class="sizer">
        <p class="sizer-title">视频分类</p>
        <div class="page">第<span>{{pageNum}}</span>页</div>
        <div class="sizer-left">
            <div class="categoryBox">
                <ul>
                    <li v-for="(item,index) in category" :key="index" >{{item.categoryName}}</li>
                </ul>
                <div class="active"></div>
            </div>
        </div>
        <div class="sizer-right">
            <ul>
                <li v-for="(assert,index) in videoList" :key="index">
                    <lazy-image
                        :src='assert.assetImg'
                        :placeholder='placeholder'
                        :imgClass="round"
                        :initScore = 'initScore'
                        :title="assert.assetName"
                        :score="assert.score"
                     />
                </li>
            </ul>
        </div>
         <!-- 滚动条 -->
    <div class="search-scroll">
        <div class="search-bar"></div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"  ref="keyDo"></keyDo>
    </div>
</template>
<style scoped>
    .sizer-title{position: absolute;width: 156px;height: 54px;top: 17px;left: 84px;font-size: 39px;color: #FFFFFF;opacity: 0.6525;box-shadow: 0px 0px blur 6px;font-family:PingFang HK, Ultralight;font-weight: 100;}
    .page{position: absolute;width: 82px;height: 33px;left: 1128px;top: 71px;font-family:PingFang HK, Ultralight;font-size: 24px;color: #FFFFFF;font-weight: 100;letter-spacing: 4px;}
    .sizer-left{position: absolute;width: 117px;height: 425px;top: 142px;left: 84px;border-radius: 20px;background-color:rgba(255, 255, 255, 0.2);display:inline;border:1px solid rgba(255, 255, 255, 0.4);padding-top:41px;overflow: hidden;padding-bottom: 41px;}
    .categoryBox{position: relative;width: 117px;height: 425px;overflow: hidden;}
    .sizer-left ul{position: relative;width: 117px;height: 425px;top:-87px;}
    .sizer-left li{width: 115px;height: 70px;line-height:70px;color: #FFFFFF;text-align: center;font-size: 20px;font-family:PingFang HK, Medium;margin-bottom:20px; }
    /* .sizer-left li.hover{border:1px solid rgba(255, 255, 255, 1);border-radius: 10px;background-color:rgba(255, 255, 255, 0.2);font-size: 22px; } */
    .sizer-right{position: absolute;width: 960px;height:530px;left: 230px;top: 129px;}
    .sizer-right li{width:160px;height: 230px;float: left;margin: 15px 0px 32px 30px;}
    .sizer-right li.hover{ box-shadow:0px 10px 10px rgba(0, 0, 0, 0.55);border:4px #FFF solid;transform: scale(1.1);margin: 11px -4px 28px 26px;border-radius: 19px;}
    .sizer-right li .imglazy{border-radius:15px;}
    .search-bar{position: relative;width: 11px;height: 56px;background-color: #FFFFFF;border: rgba(112, 112, 112, 1) 1px solid;border-radius: 10px;left: -6px;top: -1%;}
    .search-scroll{position: absolute;height: 489px;top: 137.5px;right:37.5px;width:0px;border:1px solid #ffffff;}
    .active{position:fixed;width:116px;height: 64px;left:84px;top: 369px;background-color:rgba(255, 255, 255, 0.2);border: 1px solid rgba(255, 255, 255, 1);border-radius: 10px; }
</style>
<style>
    .sizer-right li img.round{border-radius:15px;}
</style>

<script>
import {mapGetters} from 'vuex'
import {HTTPUtil as api} from '../../fetch/api.js'
import {bp as bi} from '../../js/ga.js'
import lodash from 'lodash'

export default {
    name: 'sizer',
    props:['catCode','categoryName','catId'],
    data(){
        return{
            category:[{"categoryName":"CG动画","categoryId":'1499654521269784714',"catCode":""},{"categoryName":"网络游戏","categoryId":'1499654442597670738',"catCode":""},{"categoryName":"手机游戏","categoryId":'1499654475198688880',"catCode":""},{"categoryName":"单机游戏","categoryId":'1499654488381787868',"catCode":""},{"categoryName":"主机游戏","categoryId":'1499654495939201751',"catCode":""},{"categoryName":"CG动画","categoryId":'1499654521269784714',"catCode":""}],
            videoList:[],
            isShow:true,
            pos:0,
            itemNo:0,
            pageSize:10,
            pageIndex:0,
            placeholder:require('../../assets/img_loading_160x230.png'),
            initScore:require('../../assets/corner_mark.png'),
            round:'round',
            pageNum:1,
            scrollBar:90,
            movePercent:0,
            timer:0,
            firstLoad:true,
            ajaxLoad:true,
        }
    },activated(){
        this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
        this.removeCss();
        this.pos = 0;
        this.itemNo=0;
        this.addCss();
        this.pageIndex=0;
        this.pageSize=10;
        this.videoList=[];
        this.init();
         try {
          let overTime =  this.httpResponseTime - this.startTime;
          if(this.firstLoad){
              this.firstLoad = false;
          }
           let traceId = "|"+this.catId+"|";
           let traceType = "|1|9|";
           bi.loadPage(traceId,traceType,9,0,1,this.catId,0,overTime);
        } catch (error) {
        console.log(error);
         }
    },created(){
        this.init();
    },methods:{
        init(){
            // let _this = this;
            // api.getFilter().then((JsonData)=>{
            //     console.log(JsonData);
            //     _this.category = JsonData.data.categoryList[0].dimensionMap.contentList;
            // })
            let catId = this.category[3].categoryId;
            this.getSearchResults(catId);
        },getSearchResults(catId){ // 筛选结果
            let _this = this;
            api.getFilterResult(catId, this.pageIndex, this.pageSize).then((jsonData)=>{
                _this.videoList = jsonData.data.assetList;
                _this.pageNum = 1;
                _this.scrollBar=90;
                _this.movePercent=0;
                _this.$nextTick(function () {
                    $('.search-bar').stop(false,true).animate({'top':"-1%"});
                    _this.$lazyImages.loadImage();
                })
            });

        },getNextPage(pindex){
            this.ajaxLoad = false;
            let _this = this;
            let catId = this.category[3].categoryId;
            api.getFilterResult(catId, pindex, this.pageSize).then((jsonData)=>{
                _this.videoList = jsonData.data.assetList;
                _this.ajaxLoad = true;
                _this.$nextTick(function () {
                    _this.$lazyImages.loadImage();
                    _this.addCss();
                })
            })
        },keyCode(params) {
            if (params == "down") {
                if($('.sizer-left ul').is(":animated")){return}
                 this.down();
            } else if (params == "up") {
                if($('.sizer-left ul').is(":animated")){return}
                this.up();
            } else if (params == "left") {
                this.left();
            } else if (params == "right") {
                this.right();
            } else if (params == "KeyEnter") {
                try {// 筛选资产点击
                    let asset_id = this.videoList[this.itemNo].assetId;
                    if(this.catCode==undefined)this.catCode=0;
                    if(this.catId==undefined)this.catId=0;
                    let traceId = "|"+this.catId+"|";
                    let traceType = "|1|9|";
                    bi.shaixuan(this.category[3].categoryId, traceId, traceType, 1, this.catId,"",9,0);
                } catch (error) {
                    console.log(error);
                }
              let url =  this.videoList[this.itemNo].jsonUrl;
              this.$router.push({name:'GamePages',params:{jsonUrl:url}});
            } else if (params == "KeyBack") {
                this.$router.go(-1);
            }
        },left:lodash.debounce(function(){
            if(this.pos==1){
                this.removeCss();
                if(this.itemNo==0||this.itemNo==5){
                    this.pos=0;
                }else{
                    this.itemNo--;
                }
                this.addCss();
            }
        },300),
        right:lodash.debounce(function(){
            if(this.pos==0){
                this.removeCss();
                this.pos=1;
                this.itemNo=0;
                this.addCss();
            }else if(this.pos==1){
                if(this.itemNo==(this.videoList.length-1))return;
                if(this.itemNo==9)return;
                this.removeCss();
                this.itemNo++;
                this.addCss();
            }
        },300),
        up:lodash.debounce(function(){
            if(this.pos==0){
              this.pageIndex = 0;
              let _this = this;
              let _temp = this.category[this.category.length-2];
              $('.sizer-left ul').stop(false,true).animate({top: '0px' }, 250, function () {
                  _this.category.pop()
                    _this.category.unshift(_temp);
                    $('.sizer-left ul').css('top','-87px');
                    let catId = _this.category[3].categoryId;
                    _this.getSearchResults(catId);
			  });
            }else if(this.pos==1){
                this.removeCss();
                if(this.itemNo<5&&this.pageIndex>0){
                    if(this.ajaxLoad){
                        this.pageIndex-=10;
                        this.getNextPage(this.pageIndex);
                        this.itemNo=0;
                        this.pageNum--;
                        this.movePercent = this.movePercent - this.scrollBar;
                        this.scrollBar = Math.ceil(this.scrollBar*2);
                        if(this.scrollBar>=50){
                            $('.search-bar').stop(false,true).animate({'top':"-1%"});
                            this.addCss();
                            return;
                        }
                        $('.search-bar').stop(false,true).animate({'top':this.movePercent+"%"});
                    }
                }else if(this.itemNo>4){
                    this.itemNo-=5;
                    this.addCss();
                }else if(this.itemNo<4 && this.pageNum === 1){
                    this.addCss();
                }
                // this.addCss();
            }
        },300),
        down:lodash.debounce(function(){
            if(this.pos==0){
               this.pageIndex = 0;
               let _this = this;
               this.category.push(this.category[1]);
               $('.sizer-left ul').stop(false,true).animate({top: '-174px' }, 250, function () {
                   _this.category.shift();
                    $('.sizer-left ul').css('top','-87px');
                    let catId = _this.category[3].categoryId;
                    _this.getSearchResults(catId);
			  });
            }else if(this.pos==1){ 
                this.removeCss();
                if(this.itemNo>4){
                    if(this.ajaxLoad){
                        if(this.itemNo<=(this.videoList.length-1)&&this.videoList.length<10){
                            this.addCss();
                            return;
                        };
                        this.pageIndex+=10;
                        this.getNextPage(this.pageIndex);
                        this.itemNo=0;
                        this.pageNum++;
                        this.scrollBar = Math.ceil(this.scrollBar /2);
                        this.movePercent = this.movePercent + this.scrollBar;
                        $('.search-bar').stop(false,true).animate({'top':this.movePercent+"%"});                      
                    }
                }else if(this.itemNo<5 && this.videoList.length>5){
                    this.itemNo+=5;
                    if(this.itemNo>(this.videoList.length-1))this.itemNo-=5;
                    this.addCss();
                }else if(this.itemNo<5 && this.videoList.length <=5){
                    this.addCss();
                }
                //  this.addCss();
            }
        },300),
        addCss(){
            if(this.pos==1){
                $('.sizer-right li').eq(this.itemNo).addClass('hover');
                let p_title = $('.sizer-right li:eq('+this.itemNo+') .s-title-name').text();
                if(p_title.length>=9){
                    let id = '.sizer-right li';
                    let speed = 50;
                    let that = this;
                    let tab =  document.querySelectorAll(id+' .s-title')[this.itemNo];
                    let tab1 =  document.querySelectorAll(id+' .s-title-name')[this.itemNo];
                    let tab2 =  document.querySelectorAll(id+' .s-title-temp')[this.itemNo];
                    tab.style.textOverflow = 'clip';
                    tab2.innerHTML = tab1.innerHTML;
                    this.timer = setInterval(function(){
                        that.Marquee(tab,tab1,tab2)
                    },speed);
                 }
            }else{
                $('.active').show();
            }
        },removeCss(){
            if(this.pos==1){
                $('.sizer-right li').eq(this.itemNo).removeClass('hover');
                 if(this.timer!=0){
                    clearInterval(this.timer);
                    this.timer = 0;
                    let id = '.sizer-right li';
                    let tab = document.querySelectorAll(id+' .s-title')[this.itemNo];
                    document.querySelectorAll(id+' .s-title-temp')[this.itemNo].innerHTML="";
                    tab.scrollLeft = 0;
                    tab.style.textOverflow = 'ellipsis';
                 }
            }else{
                $('.active').hide();
            }
        } , Marquee(tab,tab1,tab2){
                let a = 0;
              if(tab2.offsetWidth - tab.scrollLeft <= 0){
                  // tab.scrollLeft -= tab1.offsetWidth;
                  tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
              }else{
                  a = tab.scrollLeft;
                  tab.scrollLeft++;
                  if(a==tab.scrollLeft){
                      tab.scrollLeft=0;
                  }
              }
            }
    },computed:{
        ...mapGetters([
            'rootPath',
            'catList',
            'behindParams',
            'isHTTPS',
            'startTime',
            'httpRequestTime',
            'httpResponseTime'
        ])
    },beforeRouteLeave(to, from, next){
      try {
          console.log("开始时间被重置"+this.startTime);
           let overTime = 0;
           overTime =  new Date().getTime() - this.httpResponseTime;
           if(this.catCode==undefined)this.catCode=0;
           if(this.catId==undefined)this.catId=0;
           let traceId = "|"+this.catId+"|";
           let traceType = "|1|9|";
           this.$store.commit('GET_STARTTIME',new Date().getTime());
           bi.closPage(traceId,traceType,9,0,1,this.catId,0,overTime)
           console.log("页面停留时间："+overTime+"^^^^^^^focusId:"+this.catId);
      } catch (error) {
           console.log(error);
      }finally{
           next();
      }
  }
}
</script>



